@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);

#app{
    height: 100vh;
}
#navbar{
    display: flex;
    flex-basis: 100%;
    min-width: 280px;
    background: #005C45;
    justify-content: space-around;
    padding: 5px;
    position: -webkit-sticky;
    position: sticky;
    z-index: 20;
}
#navbar *{
    align-self: center;
    cursor: pointer;
    color: #ddd;
    font-size: 250%;
    height: 45px;
    text-decoration: none;
}
#navbar i{
    display: flex;    
}
#navbar i::before{
    align-self: center;
}


#calculadora {
    

    height: 75%;
    width: 75%;
    max-width: 450px;
    min-width: 300px;
    max-height: 650px;
    min-height: 450px;

    margin: 3em auto;
    padding: 7px 5px; 
    border: 5px solid #999;
    border-radius: 20px;

    background-image: url(/static/media/texture_background.6a0b792f.png);



    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;

}

button{
    background-color: #005C45;
    color: #ddd;
    font-weight: 700;
    padding: 1.2em;
    cursor: pointer;
    border-radius: 50px;
    border: 4px solid rgb(165, 165, 165);
    transition:background .15s;
}

button:hover {
    border: 4px solid rgb(0, 0, 0);
}

button:active{
    border-radius: 50px;
    background: #016e53;
}


#home__h2{
    color: #005C45;
    font-weight: 900;
    font-size: 200%; 
}
.boton__home{
    display: flex;
    align-items: center;
    justify-content: center;
}
#calculadora > div{
    width: 100%;
    display: flex;
    flex-grow: 1;
}
#calculadora__title{
    flex-shrink: 10;
    color: #005C45;
    font-weight: 900;
    font-size: 200%;    
}
#calculadora__form{
    flex-direction: row;
    flex-wrap: wrap;   
}
#calculadora__form > div{
    margin-left: 7px;
    margin-right: 7px;
}
#calculadora__btn{
    display:flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
}
#calculadora__btn  *{
    align-self: center;
    margin-left: 3px;
    margin-right: 3px;
}


#calculadora__res{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}
#form__inputs{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    flex-grow: 1;
}
#btn-limpiar{
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
#form__inputs > input{
    color: #005C45;
    font-weight: 900;
    font-size: 150%;
    padding: 3%;
    border: none;
    background-color: transparent;

    border-radius: 10px;
    border-bottom: 3px solid rgb(0, 92, 69);
    transition: border-bottom 0.4s,background 0.2s;
    font-size: 20px;

    min-width: 70px;
    -moz-appearance: textfield;
}
#form__inputs > input:focus {
    -webkit-appearance: none;
    outline: none;
    background-color: rgba(214, 214, 214, .5);
    
    border-bottom: 3px solid rgb(0, 207, 155);
}
#form__inputs > input:hover {
    outline: none;
    border-bottom: 3px solid rgb(0, 207, 155);
}
#form__inputs > input::-webkit-input-placeholder{
    color:rgba(0, 92, 69, 0.603);
    font-weight: 600;
}
#form__inputs > input:-ms-input-placeholder{
    color:rgba(0, 92, 69, 0.603);
    font-weight: 600;
}
#form__inputs > input::placeholder{
    color:rgba(0, 92, 69, 0.603);
    font-weight: 600;
}
#form__inputs > input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

